<template>
  <q-layout
    container
    view="LHh lpR lFf"
    class="bg-white full-width full-height"
  >
    <q-drawer
      bordered
      show-if-above
      v-model="containerSideMenu"
      side="left"
      :width="275"
      :breakpoint="850"
      :overlay="$q.screen.width <= 800"
      class="bg-white"
    >
      <q-scroll-area
        class="fit"
        :horizontal-thumb-style="{ opacity: 0 }"
        :vertical-thumb-style="{ opacity: 0 }"
      >
        <q-list class="q-pt-xl q-mx-lg">
          <q-item class="text-primary-700 q-px-none">
            <q-item-section class="text-body2 font-medium">
              {{ title }}
            </q-item-section>
          </q-item>

          <template v-if="loading">
            <q-item
              clickable
              v-ripple:secondary
              class="text-body2 font-medium q-my-xs hover-text-secondary rounded text-primary-700 hover-bg-accent"
              v-for="(item, i) in Array(6).fill({ skeleton: true })"
              :key="i"
            >
              <q-item-section avatar style="min-width: unset">
                <q-skeleton type="QToggle" size="30px" />
              </q-item-section>
              <q-item-section><q-skeleton type="rect" /></q-item-section>
            </q-item>
          </template>

          <template v-else>
            <q-item
              clickable
              v-ripple:secondary
              class="text-body2 font-medium q-my-xs hover-text-secondary rounded hover-bg-accent"
              :class="
                activeFilter == '' || activeFilter == 'all'
                  ? 'text-secondary font-medium bg-accent'
                  : 'text-primary'
              "
              @click="updateFilter('')"
            >
              <q-item-section avatar style="min-width: unset">
                <q-icon name="icon-grid-01" size="sm" />
              </q-item-section>
              <q-item-section>All Categories</q-item-section>
            </q-item>

            <q-item
              clickable
              v-ripple:secondary
              class="text-body2 font-medium q-my-xs hover-text-secondary rounded hover-bg-accent"
              v-for="(item, i) in itemsDirectory"
              :key="i"
              :class="`
              ${
                item.id === activeFilter
                  ? 'text-secondary font-medium bg-accent'
                  : 'text-primary'
              }${
                selectedOrgIsPaused ||
                (selectedOrgIsMaintenance &&
                  maintenanceTaskCatId !== item.id) ||
                (selectedOrgIsMaintenance && !isTask)
                  ? ' grayscale'
                  : ''
              }`"
              @click="updateFilter(item.id)"
            >
              <q-item-section avatar style="min-width: unset">
                <q-icon size="sm" :name="item.icon || 'icon-browser'" />
              </q-item-section>
              <q-item-section>{{ item.title }}</q-item-section>
            </q-item>

            <q-item v-if="isTask" class="q-my-xs rounded bg-none q-pa-none">
              <q-item-section class="q-pa-none">
                <q-btn
                  no-caps
                  unelevated
                  color="primary"
                  class="button-md"
                  :to="`/quick-request/${isTask ? 'task' : 'project'}`"
                >
                  <q-icon name="icon-lightning-quick" size="sm" />
                  <span class="text-body1 font-medium text-white q-pl-xs"
                    >Quick Request</span
                  >
                </q-btn>
              </q-item-section>
            </q-item>
          </template>
        </q-list>
      </q-scroll-area>
    </q-drawer>

    <q-header class="bg-white">
      <q-toolbar class="q-px-none">
        <div class="q-px-md q-pt-md q-pb-none" v-if="$q.screen.width <= 850">
          <q-btn
            flat
            dense
            size="lg"
            icon="icon-chevron-left"
            class="text-primary-400 hover-text-secondary hover-bg-accent rounded"
            @click="toogleContainerSideMenu()"
          >
            <tooltip-pro anchor="center end" self="center left">
              Show Menu
            </tooltip-pro>
          </q-btn>
        </div>
        <q-space />
        <div v-if="!noclose" class="q-px-md q-pt-md q-pb-none">
          <close-button />
        </div>
      </q-toolbar>

      <div
        class="row justify-center items-center full-width q-px-md"
        :class="$q.screen.sm || $q.screen.lt.sm ? 'q-py-md' : 'q-pb-md'"
      >
        <SearchDropdown
          :requestOptions="null"
          placeholder="What is your next request?"
          @searching="logKey"
          @blur="searchFilter = ''"
        />
      </div>
    </q-header>

    <q-page-container>
      <q-page padding>
        <q-infinite-scroll @load="loadInfiniteScroll" :offset="100">
          <div
            v-if="!visibleItemsCount && !loading"
            class="row items-center contents-center full-width full-height text-center q-py-md"
            style="height: calc(100vh - 300px) !important"
          >
            <div class="col-12">
              <div>
                <q-icon
                  size="10rem"
                  name="icon-alert-circle"
                  class="q-pr-sm text-primary-200"
                />
              </div>
              <div class="text-h5 text-primary-400">No Available Options</div>
            </div>
          </div>

          <div
            v-else
            class="row q-pa-md q-col-gutter-lg"
            :style="bottomFade ? 'margin-bottom: 200px' : ''"
            :key="items.length"
          >
            <template v-if="loading">
              <transition
                appear
                enter-active-class="animated zoomIn"
                leave-active-class="animated zoomOut"
                v-for="(item, i) in Array(6).fill({ skeleton: true })"
                :key="i"
              >
                <div class="col-12 col-md-6 col-lg-4">
                  <q-card
                    flat
                    bordered
                    v-ripple:accent
                    class="card-quicklinks full-height row hover-border-secondary"
                  >
                    <q-card-section
                      class="q-px-lg q-pt-lg q-pb-none full-width"
                    >
                      <CardIconBox useSpinner />
                      <div class="q-pt-lg">
                        <q-skeleton type="text" width="150px" />
                      </div>
                      <q-skeleton type="QChip" />
                      <q-skeleton type="text" />
                    </q-card-section>

                    <q-card-section class="q-px-lg q-pt-md q-pb-lg full-width">
                      <q-skeleton type="text" width="100px" />
                      <q-skeleton type="QToolbar" />
                    </q-card-section>
                  </q-card>
                </div>
              </transition>
            </template>
            <template v-else>
              <transition
                appear
                enter-active-class="animated zoomIn"
                leave-active-class="animated zoomOut"
                v-for="item in itemsList"
                :key="item"
              >
                <div
                  class="col-12 col-md-6 col-lg-4"
                  v-if="
                    activeFilter === '' || activeFilter === 'all'
                      ? true
                      : item.tags
                      ? item.tags?.includes?.(activeFilter)
                      : false
                  "
                >
                  <q-card
                    flat
                    bordered
                    v-ripple:accent="
                      !selectedOrgIsPaused && testOnMaintenance(item)
                    "
                    class="card-quicklinks full-height row card-padding"
                    :class="
                      !selectedOrgIsPaused && testOnMaintenance(item)
                        ? 'hover-border-secondary'
                        : 'cursor-not-allowed grayscale'
                    "
                    @click="(e) => openItem(item, e)"
                  >
                    <q-card-section class="q-pa-none">
                      <CardIconBox
                        :icon="
                          item?.icon ||
                          itemsDirectory?.[item?.tags?.[0]]?.icon ||
                          'icon-browser'
                        "
                        :iconColor="
                          !selectedOrgIsPaused && testOnMaintenance(item)
                            ? 'secondary'
                            : 'primary-700'
                        "
                      />
                      <div
                        v-if="item.turn_around_days || item.type"
                        class="q-pt-lg"
                      >
                        <q-badge rounded class="badge badge-blue">
                          {{
                            item.turn_around_days
                              ? item.turn_around_days + ' Avg. Turnaround'
                              : item.type
                          }}
                        </q-badge>
                      </div>
                      <div
                        class="text-h5 font-medium q-pt-sm q-pb-md"
                        :class="
                          !selectedOrgIsPaused && testOnMaintenance(item)
                            ? 'text-primary'
                            : 'text-primary-700'
                        "
                      >
                        {{ item.title }}
                      </div>
                      <div class="text-body2 text-primary-700">
                        {{ item.description }}
                      </div>
                    </q-card-section>

                    <q-card-section
                      v-if="item.platforms && item.platforms?.length"
                      class="q-pt-md q-pb-none q-px-none full-width"
                    >
                      <div
                        class="q-pr-sm text-body2 font-medium text-primary-700"
                      >
                        Platform{{
                          item.platforms && item.platforms?.length > 1
                            ? 's'
                            : ''
                        }}
                      </div>
                      <div class="q-pt-sm">
                        <q-scroll-area
                          visible
                          :bar-style="{
                            opacity: 0,
                            paddingTop: '50px',
                            cursor: 'pointer !important',
                          }"
                          :thumb-style="{
                            opacity: 0,
                            paddingTop: '50px',
                            cursor: 'pointer !important',
                          }"
                          class="cursor-pointer"
                          style="height: 40px; max-width: 100%"
                          @mouseover="(e) => autoScrollCondition(e, item)"
                          @mousemove="(e) => autoScrollCondition(e, item)"
                          @mouseout="(e) => stopAutoScrollCondition(e, item)"
                          @touchstart="(e) => autoScrollCondition(e, item)"
                          @touchmove="(e) => autoScrollCondition(e, item)"
                          @touchend="(e) => stopAutoScrollCondition(e, item)"
                          @mouseup.prevent
                          @mouseup.stop
                          @click.prevent
                          @click.stop
                        >
                          <!-- left cover -->
                          <div
                            style="height: 30px; width: 75px; z-index: 9"
                            class="left-scroll-cover fixed-left bg-gradient-left no-pointer-events hidden"
                          ></div>
                          <!-- main content -->
                          <div
                            class="row no-wrap q-gutter-sm q-pr-xl"
                            @click.prevent
                            @click.stop
                          >
                            <template
                              v-for="platform in item.platforms"
                              :key="platform"
                            >
                              <q-badge
                                rounded
                                class="bg-primary-100 text-primary-700 q-px-sm q-py-xs text-caption font-medium no-pointer-events"
                              >
                                <q-icon
                                  :name="formatPlatformIconSrc(platform)"
                                  class="q-pr-xs"
                                />
                                {{
                                  getPlatformInfo(platform, 'title') || platform
                                }}
                              </q-badge>
                            </template>
                          </div>
                          <!-- right cover -->
                          <div
                            style="height: 30px; width: 75px; z-index: 9"
                            class="right-scroll-cover fixed-right bg-gradient-right no-pointer-events"
                          ></div>
                        </q-scroll-area>
                      </div>
                    </q-card-section>
                  </q-card>
                </div>
              </transition>
            </template>
          </div>

          <div
            v-if="bottomFade"
            class="full-width fixed-bottom bg-gradient no-pointer-events"
            style="height: 300px"
          ></div>

          <template v-slot:loading>
            <div
              class="row justify-center q-my-md relative-position"
              style="z-index: 7"
            >
              <q-spinner-dots color="secondary" size="40px" />
            </div>
          </template>
        </q-infinite-scroll>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent } from 'vue';
import { searchInObjectArray } from 'src/assets/scripts/functions';
import SearchDropdown from 'src/components/Search/CustomDropdown.vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';

export default defineComponent({
  name: 'RequestItem',

  components: { SearchDropdown, CardIconBox },

  props: {
    bottomFade: {
      type: Boolean,
      default: false,
    },
    noclose: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: 'Item Directory',
    },
    directoryType: {
      type: String,
      default: 'tasks', // task or project
    },
  },

  watch: {
    activeFilter: {
      async handler(val) {
        this.itemsList = this.visibleItems.slice(0, 10);
      },
    },
  },

  data() {
    return {
      prevTitle: document.title,
      containerSideMenu: false,
      activeFilter: '',
      itemsList: [],
      searchFilter: '',
      scrollHoverInterval: null,
      loading: true,
    };
  },

  async mounted() {
    if (this.items.length) {
      // Preloaded Just sync
      if (this.isTask) {
        this.getTaskDirectory();
        this.getTaskCategories();
      } else {
        this.getProjectDirectory();
        this.getProjectCategories();
      }
    } else {
      // Await to Initialize
      if (this.isTask) {
        await this.getTaskDirectory();
        await this.getTaskCategories();
      } else {
        await this.getProjectDirectory();
        await this.getProjectCategories();
      }
    }
    this.prevTitle = this.$root?.config?.metaData?.title;
    this.$root.setMeta({ title: this.title });
    this.itemsList = this.visibleItems.slice(0, 10);
    if (
      this.selectedOrgIsMaintenance &&
      this.isTask &&
      this.maintenanceTaskCatId
    ) {
      // Open Maintenance Plan Task
      this.updateFilter(this.maintenanceTaskCatId);
    }
    this.loading = false;
  },

  computed: {
    ...mapState(useUser, ['selectedOrgIsMaintenance', 'selectedOrgIsPaused']),
    ...mapState(useGrowmodo, [
      'taskDirectory',
      'taskDirectory_Categories',
      'projectDirectory',
      'projectDirectory_Categories',
      'maintenanceTaskCatId',
    ]),

    isTask() {
      return this.directoryType === 'tasks';
    },

    isProject() {
      return this.directoryType === 'projects';
    },

    items() {
      return Object.values(
        this.isTask ? this.taskDirectory : this.projectDirectory
      );
    },

    itemsDirectory() {
      return this.isTask
        ? this.taskDirectory_Categories
        : this.projectDirectory_Categories;
    },

    visibleItems() {
      return this.searchFilter
        ? searchInObjectArray(this.itemWithFilter, this.searchFilter, [
            'label',
            'title',
            'description',
          ])
        : this.itemWithFilter;
    },

    itemWithFilter() {
      return this.activeFilter === '' || this.activeFilter === 'all'
        ? this.items
        : this.items.filter((e) => e.tags.includes(this.activeFilter));
    },

    visibleItemsCount() {
      return this.visibleItems?.length || 0;
    },
  },

  methods: {
    ...mapActions(useGrowmodo, [
      'formatPlatformIconSrc',
      'getPlatformInfo',
      'getTaskDirectory',
      'getProjectDirectory',
      'getTaskCategories',
      'getProjectCategories',
    ]),

    openItem(item, e) {
      if (!item) return;
      if (this.selectedOrgIsPaused) {
        // Subscription is Pause
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Your subscription is paused, please resume it first.',
        });
        return;
      }
      if (!this.testOnMaintenance(item)) {
        // Maintenance Plan Only
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Not available in maintenance plan.',
        });
        return;
      }
      this.$root.requestAction(this.isTask ? 'task' : 'project', item);
      this.$root.toggleRequest(this.isTask ? 'task' : 'project', false);
    },

    isMaintenanceTask(task) {
      return this.isTask && task?.tags?.includes?.(this.maintenanceTaskCatId);
    },

    testOnMaintenance(item) {
      if (this.selectedOrgIsMaintenance) {
        // Subscription is on Maintenance Plan
        if (!this.isMaintenanceTask(item)) return false;
      }
      return true;
    },

    updateFilter(filter) {
      this.activeFilter = filter;
    },

    toogleContainerSideMenu(force = null) {
      this.containerSideMenu = force !== null ? force : !this.containerSideMenu;
    },

    loadInfiniteScroll(index, done) {
      if (this.visibleItems.length <= this.itemsList.length) {
        done();
      }
      setTimeout(() => {
        let newItems = this.visibleItems.slice(
          this.itemsList.length,
          this.itemsList.length + 10
        );
        newItems.forEach((e) => {
          this.itemsList.push(e);
        });
        done();
      }, 250);
    },

    logKey({ val, update }) {
      this.searchFilter = val;
    },

    autoScrollCondition(evt, item) {
      if (!this.selectedOrgIsPaused && this.testOnMaintenance(item)) {
        this.hoverHere(evt);
      }
    },

    stopAutoScrollCondition(evt, item) {
      if (!this.selectedOrgIsPaused && this.testOnMaintenance(item)) {
        this.hoverEnd(evt);
      }
    },

    hoverHere(evt) {
      if (this.scrollHoverInterval) {
        this.hoverEnd();
        this.scrollHoverInterval = null;
      }
      let elX = evt.clientX || evt.layerX || 0;
      const origTarget = evt.target,
        parent = origTarget?.parentElement;
      const container = parent?.querySelector?.('.q-scrollarea__container'),
        containerRect = container?.getBoundingClientRect?.();
      let elOffsetLeft = containerRect?.left || origTarget?.offsetLeft || 0,
        elWidth = origTarget?.offsetWidth || 0,
        rightSide = elX - elOffsetLeft > elWidth / 2;

      if (!this.scrollHoverInterval && container) {
        this.scrollTarget(container, rightSide);
      }
    },

    async scrollTarget(target, rightSide) {
      const scroll_interval = 5;
      if (target.scrollLeft > 5) {
        target
          .querySelector('.left-scroll-cover')
          ?.classList?.remove?.('hidden');
      } else {
        target.querySelector('.left-scroll-cover')?.classList?.add?.('hidden');
      }
      if (rightSide) {
        this.scrollHoverInterval = setInterval(async () => {
          target.scrollLeft += scroll_interval;
        }, 50);
      } else {
        this.scrollHoverInterval = setInterval(async () => {
          target.scrollLeft -= scroll_interval;
        }, 50);
      }
    },

    hoverEnd() {
      if (this.scrollHoverInterval) {
        try {
          clearInterval(this.scrollHoverInterval);
        } catch (e) {}
      }
    },
  },

  beforeUnmount() {
    this.hoverEnd();
    this.$root.setMeta({ title: this.prevTitle });
  },
});
</script>
